.social-list {
	@include listreset;

	display: flex;
	flex-wrap: wrap;
	align-items: center;
	font-size: 22px;

	@include media-breakpoint-down(sm) {
		justify-content: space-between !important;
		max-width: 380px;
		margin-left: auto;
		margin-right: auto;
	}

	&.justify-content-end {
		.social-list__item {
			margin-right: 0 !important;

			&:not(:first-child) {
				@include media-breakpoint-up(sm) {
					margin-left: 20px;
				}

				@include media-breakpoint-up(sm) {
					margin-left: 40px;
				}

				@include media-breakpoint-up(xxl) {
					margin-left: 65px;
				}
			}
		}
	}

	&__item {
		margin-top: 5px;
		margin-bottom: 5px;

		@include media-breakpoint-down(sm) {
			margin-left: 12px;
			margin-right: 12px;
		}

		&:not(:last-child) {
			@include media-breakpoint-up(sm) {
				margin-right: 40px;
			}

			@include media-breakpoint-up(xxl) {
				margin-right: 65px;
			}
		}
	}

	&__link {
		color: $white;
		transition: color 0.3s;

		&:hover {
			color: $primary;
		}
	}
}