.nav-with-border {
	@include listreset;

	@include media-breakpoint-up(sm) {
		display: flex;
		flex-wrap: wrap;
	}

	&.justify-content-end {
		.nav-with-border__item {
			&:not(:last-child) {
				@include media-breakpoint-up(sm) {
					padding-right: 0;
				}
			}

			&:not(:first-child) {
				@include media-breakpoint-up(sm) {
					padding-left: 20px;
				}
			}

			&:first-child {
				&:after {
					@include media-breakpoint-up(sm) {
						content: none;
					}
				}
			}

			&:after {
				@include media-breakpoint-up(sm) {
					content: '';
					left: 9px;
					right: auto;
				}
			}
		}
	}

	&__item {
		@include media-breakpoint-up(sm) {
			position: relative;
		}

		&:not(:last-child) {
			margin-bottom: 15px;

			@include media-breakpoint-up(sm) {
				padding-right: 20px;
				margin-bottom: 0;
			}
		}

		&:last-child {
			&:after {
				@include media-breakpoint-up(sm) {
					content: none;
				}
			}
		}

		&:after {
			@include media-breakpoint-up(sm) {
				content: '';
				position: absolute;
				top: 8px;
				right: 11px;
				display: block;
				width: 2px;
				height: 12px;
				background-color: rgba($white, 0.5);
			}
		}
	}

	&__link {
		font-size: 17px;
		font-weight: 700;
		color: rgba($white, 0.5);
		transition: color 0.3s;

		&:hover {
			color: $white;
			text-decoration: none;
		}
	}
}