.progress-list {
	@include listreset;

	position: relative;
	display: flex;
	justify-content: center;
	counter-reset: progressList;
	overflow: hidden;
	padding-bottom: 30px;
	margin-bottom: 25px;
	border-bottom: 1px solid $mustard;

	@include media-breakpoint-up(sm) {
		padding-bottom: 40px;
	}

	@include media-breakpoint-up(md) {
		margin-bottom: 40px;
	}

	&:before {
		content: '';
		position: absolute;
		bottom: 0;
		left: 50%;
		display: block;
		height: 30px;
		width: 1px;
		background-color: $mustard;

		@include media-breakpoint-up(sm) {
			height: 40px;
		}
	}

	&__item {
		position: relative;
		display: inline-flex;
		flex-direction: column;
		align-items: center;
		margin-bottom: 10px;
		width: 275px;

		@include media-breakpoint-up(sm) {
			margin-bottom: 20px;
		}

		&:after {
			content: '';
			position: absolute;
			top: 25px;
			left: 50%;
			z-index: 1;
			display: block;
			height: 1px;
			width: 100%;
			background-color: $mustard;

			@include media-breakpoint-up(xs) {
				top: 36px;
			}
		}

		&:last-child {
			&:after {
				content: none;
			}
		}
	}

	&__button {
		display: inline-block;
		border: none;
		background-color: transparent;
		padding: 0;
		font-size: 20px;
		color: $blue-dark;

		@include media-breakpoint-up(xs) {
			font-size: 24px;
		}

		&:not(.active) {
			&:hover {
				&:before {
					background-color: $mustard;
					color: $white;
				}
			}
		}

		&.active {
			&:before {
				background-color: $primary;
				color: $blue-dark;
				border-color: $primary;
			}
		}

		&:before {
			@include sizeElement(50px);
			@include flexCenter;

			position: relative;
			z-index: 5;
			counter-increment: progressList;
			content: counter(progressList);
			border-radius: 50%;
			background-color: $white;
			border: 1px solid $mustard;
			color: $mustard;
			font-family: $headings-font-family;
			font-size: 20px;
			margin-left: auto;
			margin-right: auto;
			margin-bottom: 10px;
			outline: 10px solid $white;
			transition: background-color 0.3s, border-color 0.3s, color 0.3s;

			@include media-breakpoint-up(xs) {
				@include sizeElement(72px);

				font-size: 24px;
				margin-bottom: 20px;
			}

			@include media-breakpoint-up(md) {
				outline-width: 25px;
			}
		}
	}
}