.round-icon-and-text-item {
	display: flex;
	align-items: center;
	max-width: 400px;
	margin-bottom: 60px;

	@include media-breakpoint-up(md) {
		margin-bottom: 30px;
		padding-right: 30px;
	}

	&__icon-block {
		@include sizeElement(100px);
		@include inlineFlexCenter;

		border-radius: 50px;
		border: 1px solid $blue-dark;
	}

	&__icon {
		max-width: 50px;
		flex-shrink: 0;
	}

	&__icon-col {
		flex: 0 0 auto;
		width: 100px;
	}

	&__text-col {
		flex: 1 0 0%;
		padding-left: 25px;
	}
}