.footer {
	position: relative;
	background-color: $blue-extra-dark;
	color: rgba($white, 0.5);
	padding: 25px 0 45px;

	@include media-breakpoint-up(sm) {
		padding-top: 50px;
	}

	@include media-breakpoint-up(xl) {
		padding-top: 70px;
	}

	&__main-content {
		position: relative;
		z-index: 15;
	}

	&__top-content {
		border-top: 2px solid $aqua;
		padding: 30px 0 25px;

		@include media-breakpoint-up(lg) {
			padding: 25px 0 20px;
		}
	}

	&__logo {
		display: block;
		max-width: 65px;
		margin: 0 auto 30px;

		@include media-breakpoint-up(lg) {
			max-width: 100px;
			margin: 0;
		}
	}

	&__btn-list {
		margin-bottom: 25px;

		@include media-breakpoint-up(lg) {
			margin-bottom: 0;
		}
	}

	&__bottom-content {
		border-top: 1px solid $aqua;
		padding-top: 30px;

		@include media-breakpoint-up(lg) {
			padding-top: 50px;
		}

		@include media-breakpoint-up(xl) {
			padding-top: 75px;
		}
	}

	&__nav {
		margin-bottom: 30px;

		@include media-breakpoint-up(lg) {
			margin-bottom: 50px;
		}

		@include media-breakpoint-up(xl) {
			margin-bottom: 75px;
		}
	}

	&__text {
		font-size: 16px;
		line-height: 1.7647;
		font-weight: 400;
		margin-bottom: 30px;

		@include media-breakpoint-up(xs) {
			font-size: 17px;
		}

		@include media-breakpoint-up(lg) {
			margin-bottom: 65px;
		}
	}

	&__copyright {
		font-size: 16px;
		margin-bottom: 35px;

		@include media-breakpoint-up(lg) {
			font-size: 17px;
		}

		@include media-breakpoint-up(xl) {
			margin-bottom: 0;
		}
	}
}