.success-item {
	text-align: center;
	max-width: 295px;
	margin: 0 auto 55px;

	&__title {
		font-size: 22px;
		font-weight: 400;
		font-family: inherit;
		margin-bottom: 5px;

		@include media-breakpoint-up(xs) {
			font-size: 30px;
			margin-bottom: 0;
		}
	}

	&__value {
		font-size: 55px;
		margin-bottom: 10px;

		@include media-breakpoint-up(xs) {
			font-size: 72px;
		}
	}

	&__text {
		font-weight: 400;
	}
}