.video-block {
	@include bgCover;
	@include responsive-ratio(372, 335);

	@include media-breakpoint-up(sm) {
		@include responsive-ratio(1641, 903);
	}

	position: relative;
	display: block;
	background-color: $black;
	cursor: pointer;

	&:hover {
		.video-block__play-btn {
			transform: translate(-50%, -50%) scale(0.8);
		}

		&:after {
			opacity: 1;
		}
	}

	&:after {
		@include absoluteCover;

		z-index: 2;
		content: '';
		display: block;
		background-color: rgba($black, 0.3);
		opacity: 0;
		transition: opacity 0.3s;
	}

	&__play-btn {
		@include sizeElement(100px);
		@include absoluteCenter;

		z-index: 5;
		border-radius: 50%;
		background-color: $aqua;
		transition: transform 0.3s;

		@include media-breakpoint-up(xs) {
			@include sizeElement(133px);
		}

		@include media-breakpoint-up(xl) {
			@include sizeElement(163px);
		}

		&:after {
			@include border-arrow(20, 23, $blue-dark, r);

			@include media-breakpoint-up(xl) {
				@include border-arrow(24, 28, $blue-dark, r);
			}

			content: '';
			position: absolute;
			top: 50%;
			left: 53%;
			transform: translate(-50%, -50%);
			display: block;
		}
	}

	&__decoration-block {
		position: absolute;
		z-index: 10;
		max-width: 200px;
		width: 100%;

		@include media-breakpoint-up(md) {
			max-width: 350px;
		}

		@include media-breakpoint-up(xl) {
			max-width: 480px;
		}

		&.top-left {
			top: 30px;
			left: -30px;

			@include media-breakpoint-up(lg) {
				top: 55px;
				left: -80px;
			}
		}

		&.bottom-right {
			bottom: 20px;
			right: -45px;

			@include media-breakpoint-up(lg) {
				bottom: 0;
			}
		}
	}

	&__decoration-block-item {
		@include bgCover;

		display: inline-flex !important;
		align-items: center;
		justify-content: center;
		min-height: 44px;
		width: 100%;
		color: $white;
		padding: 5px 15px;

		@include media-breakpoint-up(md) {
			max-width: 350px;
			min-height: 80px;
			padding: 15px 30px;
		}

		@include media-breakpoint-up(xl) {
			max-width: 480px;
			min-height: 105px;
		}
	}
}