.steps-list {
	@include listreset;

	counter-reset: steps-list;
	margin-bottom: 40px;

	@include media-breakpoint-up(xs) {
		margin-bottom: 60px;
	}

	&__item {
		position: relative;
		padding: 10px 0 0 75px;

		&:not(:last-child) {
			margin-bottom: 20px;

			@include media-breakpoint-up(xs) {
				margin-bottom: 35px;
			}

			@include media-breakpoint-up(md) {
				margin-bottom: 40px;
			}
		}

		&:before {
			@include flexCenter;
			@include sizeElement(50px);

			position: absolute;
			top: 0;
			left: 0;
			counter-increment: steps-list;
			content: counter(steps-list);
			border: 1px solid $mustard;
			border-radius: 50%;
			color: $mustard;
			font-weight: 700;
			font-size: 24px;
			font-family: $headings-font-family;
		}
	}

	&__item-title {
		font-family: inherit;
		font-weight: 600;
	}
}