.section {
	position: relative;

	&__main-content {
		position: relative;
		z-index: 15;
	}

	&-default {
		padding: 60px 0;
		font-weight: 400;

		@include media-breakpoint-up(md) {
			padding: 80px 0;
		}

		@include media-breakpoint-up(lg) {
			padding: 100px 0;
		}

		@include media-breakpoint-up(xl) {
			padding: 120px 0;
		}

		ul {
			@extend .list-with-dots;

			margin-bottom: 30px;
		}
	}

	&-welcome {
		position: relative;
		padding: 220px 0 120px;
		background-color: $blue-dark;

		@include media-breakpoint-up(xs) {
			padding: 280px 0 200px;
		}

		@include media-breakpoint-up(xxl) {
			padding-bottom: 230px;
		}

		&.short {
			padding: 220px 0 120px;

			@include media-breakpoint-up(lg) {
				padding-top: 280px;
			}
		}

		&__bg {
			@include absoluteCover;
			@include bgCover;

			opacity: 0.5;
			pointer-events: none;
		}
	}

	&-achieve {
		&__round-icon-and-text-items {
			padding: 60px 0 0;
			border-bottom: 1px solid $blue-dark;

			@include media-breakpoint-up(md) {
				padding-bottom: 30px;
			}
		}

		&__cards-content {
			padding: 50px 0 20px;

			@include media-breakpoint-up(xl) {
				padding-top: 90px;
			}
		}

		&__title {
			margin-bottom: 50px;

			@include media-breakpoint-up(xl) {
				margin-bottom: 85px;
			}
		}
	}

	&-successes {
		padding: 50px 0 20px;

		&__title {
			margin-bottom: 45px;

			@include media-breakpoint-up(md) {
				margin-bottom: 70px;
			}
		}
	}

	&-steps {
		padding: 50px 0 40px;
		overflow: hidden;

		@include media-breakpoint-up(xs) {
			padding: 50px 0 75px;
		}

		@include media-breakpoint-up(xl) {
			padding-bottom: 130px;
		}

		&__wrap {
			background: linear-gradient(to bottom, $white 250px, $blue-dark 250px);
			margin-left: -20px;
			margin-right: -20px;
			padding-bottom: 40px;

			@include media-breakpoint-up(xs) {
				padding-bottom: 80px;
			}

			@include media-breakpoint-up(sm) {
				margin-left: 1px;
				margin-right: 1px;
			}

			@include media-breakpoint-up(xxl) {
				padding-bottom: 95px;
			}
		}

		&__main-block {
			background-color: $white;
			border: 1px solid $mustard;
			padding: 40px 10px;
			margin: 0 20px;

			@include media-breakpoint-up(xs) {
				padding: 65px 10px;
			}

			@include media-breakpoint-up(sm) {
				padding-left: 30px;
				padding-right: 30px;
				margin: 0 30px;
			}

			@include media-breakpoint-up(lg) {
				padding: 65px 75px 70px;
			}

			@include media-breakpoint-up(xl) {
				margin: 0;
			}
		}
	}

	&-global {
		padding: 40px 0;
		background-repeat: no-repeat;
		background-position: left -170px center;
		background-size: 1500px;

		@include media-breakpoint-up(xs) {
			padding: 75px 0 80px;
		}

		@include media-breakpoint-up(md) {
			background-size: contain;
			background-position: center;
		}

		@include media-breakpoint-up(xl) {
			padding: 130px 0 80px;
		}

		&__title {
			margin-bottom: 80px;

			@include media-breakpoint-up(xs) {
				margin-bottom: 110px;
			}

			@include media-breakpoint-up(xl) {
				margin-bottom: 150px;
			}
		}

		&__achievements-items {
			margin-bottom: 100px;

			@include media-breakpoint-up(sm) {
				margin-bottom: 0;
			}
		}
	}

	&-image-and-content {
		padding: 40px 0 140px;

		@include media-breakpoint-up(md) {
			padding: 80px 0 115px;
		}

		&__img {
			@include media-breakpoint-down(sm) {
				margin-left: -20px;
				margin-right: -20px;
				max-width: calc(100% + 40px);
			}
		}

		&__content {
			padding-top: 60px;

			@include media-breakpoint-up(md) {
				padding-top: 30px;
			}

			@include media-breakpoint-up(lg) {
				padding-top: 45px;
			}

			@include media-breakpoint-up(xxl) {
				padding-top: 65px;
			}
		}
	}

	&-with-video {
		padding: 50px 0 25px;
		background: linear-gradient(to bottom, $blue-dark 50%, $blue-extra-dark 30%);
		overflow: hidden;

		@include media-breakpoint-up(sm) {
			padding-bottom: 50px;
		}

		@include media-breakpoint-up(md) {
			background: linear-gradient(to bottom, $blue-dark 30%, $blue-extra-dark 30%);
		}

		@include media-breakpoint-up(xl) {
			padding: 70px 0;
		}

		@include media-breakpoint-up(xxl) {
			padding-top: 125px;
		}
	}
}