body {
	@include customScrollbar(10px, $blue-dark, $primary);

	min-width: 320px;

	@include media-breakpoint-down(sm) {
		font-size: 16px;
	}
}

.main {
	overflow: hidden;
}

.container {
	@include media-breakpoint-down(sm) {
		padding-left: 20px;
		padding-right: 20px;
	}
}

.row {
	@include media-breakpoint-down(sm) {
		margin-left: -20px;
		margin-right: -20px;
	}

	.col,
	[class^="col"] {
		@include media-breakpoint-down(sm) {
			padding-left: 20px;
			padding-right: 20px;
		}
	}
}