.header {
	background-color: transparent;
	padding: 30px 0;
	transition: padding 0.5s, background-color 0.5s;

	@include media-breakpoint-up(xl) {
		padding: 55px 0;
	}

	&.active {
		padding: 10px 0;
		background-color: $blue-dark;

		@include media-breakpoint-up(xl) {
			padding: 20px 0;
		}

		.header__logo {
			max-width: 35px;

			@include media-breakpoint-up(xl) {
				max-width: 50px;
			}
		}

		.header__menu {
			@include media-breakpoint-up(xl) {
				transform: translate(0, 0);
			}
		}
	}

	&__main-content {
		display: flex;
		align-items: center;
		justify-content: space-between;
	}

	&__logo {
		display: inline-block;
		max-width: 65px;
		flex-shrink: 0;
		transition: max-width 0.5s;

		@include media-breakpoint-up(xl) {
			max-width: 75px;
		}

		@include media-breakpoint-up(xxl) {
			max-width: 100px;
		}

		+ .header__menu {
			@include media-breakpoint-up(xl) {
				padding-left: 30px;
			}

			@include media-breakpoint-up(xxl) {
				padding-left: 40px;
			}
		}
	}

	&__btn {
		flex-shrink: 0;
		max-width: 285px;
		width: 100%;

		@include media-breakpoint-up(xxl) {
			max-width: 335px;
		}
	}

	&__menu {
		@include media-breakpoint-down(xl) {
			position: fixed;
			top: 0;
			left: 0;
			right: 0;
			height: 100vh;
			height: calc(var(--vh, 1vh) * 100);
			display: flex;
			align-items: center;
			opacity: 0;
			visibility: hidden;
			background-color: $blue-dark;
			overflow: auto;
			padding: 80px 0;
			transition: opacity 0.3s, visibility 0.3s;

			&.active {
				opacity: 1;
				visibility: visible;
			}
		}

		@include media-breakpoint-up(xl) {
			flex-grow: 1;
			transform: translate(0, -30px);
			transition: transform 0.5s;
		}

		@include media-breakpoint-up(xxl) {
			transform: translate(0, -40px);
		}
	}

	&__container {
		margin: auto;

		@include media-breakpoint-up(xl) {
			display: flex;
			align-items: center;
			justify-content: space-between;
			padding: 0;
		}
	}

	&__nav {
		margin-bottom: 50px !important;

		@include media-breakpoint-down(xl) {
			display: block !important;
		}

		@include media-breakpoint-up(xl) {
			padding-right: 20px !important;
			margin-bottom: 0 !important;
		}

		.navigation-manu__item {
			@include media-breakpoint-down(xl) {
				margin-top: 0;
				margin-bottom: 0;
			}

			&:not(:last-child) {
				@include media-breakpoint-down(xl) {
					margin-bottom: 20px;
				}
			}
		}
	}
}