.background-grid {
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	z-index: 10;
	pointer-events: none;

	&.on-dark {
		.background-grid__col {
			border-color: rgba($white, 0.07);
		}
	}

	&.on-white {
		.background-grid__col {
			border-color: rgba($blue-dark, 0.07);
		}
	}

	&__container {
		@include absoluteCover;
	}

	&__row {
		height: 100%;
	}

	&__col {
		height: 100%;
		border-right: 1px solid transparent;

		&:first-child {
			border-left: 1px solid transparent;
		}

		&:last-child {
			transform: translate(-$container-padding-x, 0);
		}

		&:not(:last-child) {
			transform: translate($container-padding-x, 0);
		}
	}
}

.bg-grid-shift {
	@include media-breakpoint-up(sm) {
		margin-right: -$grid-gutter-width;
	}
}