.navigation-menu {
	@include listreset;

	@include media-breakpoint-up(sm) {
		display: flex;
		flex-wrap: wrap;
	}

	&.justify-content-center {
		.navigation-menu__item {
			@include media-breakpoint-up(sm) {
				margin-left: 12px;
				margin-right: 12px;
			}

			@include media-breakpoint-up(xxl) {
				margin-left: 20px;
				margin-right: 20px;
			}
		}
	}

	&__item {
		@include media-breakpoint-up(sm) {
			margin-top: 5px;
			margin-bottom: 5px;
		}

		&:not(:last-child) {
			@include media-breakpoint-down(sm) {
				margin-bottom: 20px;
			}

			@include media-breakpoint-up(sm) {
				margin-right: 24px;
			}

			@include media-breakpoint-up(xxl) {
				margin-right: 40px;
			}
		}
	}

	&__link {
		position: relative;
		display: inline-block;
		font-weight: 700;
		text-transform: uppercase;
		font-size: 17px;
		color: $white;

		@include media-breakpoint-up(xl) {
			font-size: 16px;
		}

		@include media-breakpoint-up(xxl) {
			font-size: 17px;
		}

		&:hover {
			text-decoration: none;
			color: $white;

			&:after {
				transform: scaleX(1);
				transform-origin: left center;
			}
		}

		&:after {
			content: "";
			position: absolute;
			bottom: -3px;
			left: 0;
			width: 100%;
			height: 2px;
			background: linear-gradient(254.54deg, $primary 0%, darken($primary, 20%) 100%);
			transform: scaleX(0);
			transform-origin: right center;
			transition: transform 0.5s cubic-bezier(0.694, 0, 0.335, 1);
		}
	}
}