.animation-item {
	opacity: 0;
	transition: opacity 1s, transform 1s;

	&.active {
		opacity: 1;
		transform: translate(0, 0) !important;
	}

	&.from-top {
		transform: translate(0, -75px);
	}

	&.from-bottom {
		transform: translate(0, 75px);
	}

	&.from-left {
		transform: translate(-75px, 0);
	}

	&.from-right {
		transform: translate(75px, 0);
	}
}