.btn {
	position: relative;
	display: inline-flex;
	align-items: center;
	justify-content: center;
	width: 100%;
	text-transform: uppercase;
	font-weight: 700;
	letter-spacing: 0.36px;

	&:after {
		content: "";
		position: absolute;
		top: -$input-btn-border-width;
		bottom: -$input-btn-border-width;
		left: -$input-btn-border-width;
		z-index: 1;
		width: calc(100% + #{$input-btn-border-width} + #{$input-btn-border-width});
		transform: scaleX(0);
		transform-origin: right center;
		transition: transform 0.5s cubic-bezier(0.694, 0, 0.335, 1);
	}

	&:hover {
		&:after {
			transform: scaleX(1);
			transform-origin: left center;
		}
	}

	> span {
		position: relative;
		z-index: 5;
	}

	&-primary {
		color: $blue-dark;

		&:after {
			background: linear-gradient(254.54deg, $primary 0%, darken($primary, 20%) 100%);
		}

		&:focus,
		&:active,
		&:hover {
			color: $blue-dark;
			background-color: $primary;
			border-color: $primary;
		}
	}

	&-aqua {
		color: $blue-dark;

		&:after {
			background: linear-gradient(254.54deg, $aqua 0%, darken($aqua, 20%) 100%);
		}

		&:focus,
		&:active,
		&:hover {
			color: $blue-dark;
			background-color: $aqua;
			border-color: $aqua;
		}
	}

	&:not(.btn-sm):not(.btn-lg) {
		min-height: 80px;
		max-width: 350px;

		@include media-breakpoint-down(xxl) {
			font-size: 17px;
			max-width: 325px;
		}

		@include media-breakpoint-down(xs) {
			font-size: 13px;
			min-height: 70px;
		}
	}

	&-list {
		@include listreset;

		display: flex;
		flex-wrap: wrap;

		.text-center & {
			justify-content: center;

			.btn-list__item {
				@include media-breakpoint-up(sm) {
					margin-left: 7px;
					margin-right: 7px;
				}
			}
		}

		&__item {
			display: inline-flex;
			margin-top: 8px;
			margin-bottom: 8px;
			width: 100%;

			@include media-breakpoint-up(sm) {
				max-width: 325px;
			}

			@include media-breakpoint-up(xxl) {
				max-width: 350px;
			}

			&:not(:last-child) {
				@include media-breakpoint-up(sm) {
					margin-right: 14px;
				}
			}
		}

		&__btn {
			@include media-breakpoint-down(sm) {
				max-width: 100% !important;
			}
		}
	}
}