@include title-sizes();

.text-block {
	p:last-child {
		margin-bottom: 0;
	}
}

.text-sm {
	font-size: $font-size-sm;
}

b, strong {
	font-weight: $font-weight-bold;
}

ul {
	&.list-with-dots {
		@include listreset;

		li {
			position: relative;
			padding-left: 15px;

			&:not(:last-child) {
				margin-bottom: 30px;
			}

			&:before {
				content: "•";
				position: absolute;
				top: 0;
				left: 0;
				z-index: 5;
				display: inline-block;
			}
		}
	}

	&.list-3-cols {
		display: flex;
		flex-wrap: wrap;

		li {
			flex: 0 0 auto;
			width: 100%;

			@include media-breakpoint-up(md) {
				flex: 0 0 auto;
				width: 50%;
				padding-right: 30px;
			}

			@include media-breakpoint-up(lg) {
				flex: 0 0 auto;
				width: 33.33333333%;
			}

			@include media-breakpoint-up(xl) {
				padding-right: 50px;
			}
		}
	}
}

h1, .h1,
h2, .h2,
h3, .h3,
h4, .h4,
h5, .h5,
h6, .h6 {
	@include media-breakpoint-down(sm) {
		line-height: 1.045454545;
	}

	mark {
		background-color: transparent;
		color: $aqua;
		padding: 0;
	}

	&.title-with-lines-bg {
		@include bgCover;

		padding: 25px 10px;
		text-align: center;

		@include media-breakpoint-up(sm) {
			padding: 50px;
		}

		@include media-breakpoint-up(xl) {
			padding: 60px 125px;
		}
	}
}

h1, .h1 {
	margin-bottom: 50px;
}

h2, .h2 {
	margin-bottom: 35px;
}

h3, .h3 {
	margin-bottom: 20px;

	@include media-breakpoint-up(md) {
		margin-bottom: 25px;
	}

	&.title-with-lines-bg {
		margin-bottom: 40px;
	}
}

h4, .h4 {
	margin-bottom: 20px;
}

h5, .h5 {
	margin-bottom: 15px;
}

h6, .h6 {
	margin-bottom: 15px;
}

.bordered-text {
	display: inline-block;
	font-size: 24px;
	letter-spacing: 0.48px;
	text-transform: uppercase;
	padding: 25px 15px;
	border-top: 1px solid $blue-dark;
	border-bottom: 1px solid $blue-dark;
	margin-bottom: 35px;

	@include media-breakpoint-up(xs) {
		padding: 25px 70px;
	}
}