.card-achieve {
	max-width: 470px;
	padding-top: 15px;
	border-top: 2px solid $aqua;
	margin: 0 0 50px 0;

	@include media-breakpoint-up(sm) {
		margin-right: 30px;
	}

	&__img-block {
		@include responsive-ratio(470, 410);

		position: relative;
		overflow: hidden;
	}

	&__body {
		padding-top: 45px;
		text-align: center;

		@include media-breakpoint-up(sm) {
			text-align: left;
		}
	}
}